.hero {
  background-image: url("Background.4a368515.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  padding: 64px 0 100px;
  position: relative;
}

.hero:before {
  content: "";
  opacity: .7;
  background-color: #000;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.hero__image, .hero__text {
  z-index: 2;
  position: relative;
}

.hero__image {
  height: 250px;
  margin: 0 auto;
  display: flex;
}

.hero__text {
  text-align: center;
  margin: 64px 0;
  font-size: 40px;
  font-weight: bold;
  line-height: 56px;
}

.info-bar {
  z-index: 2;
  background-color: #fff;
  justify-content: space-between;
  padding: 16px 24px;
  display: flex;
  position: relative;
}

.info-bar__info {
  gap: 40px;
  display: flex;
}

.info-bar__info__item {
  color: #000;
}

.info-bar__info__item b {
  color: #000;
  font-size: 18px;
  display: block;
}

.button {
  white-space: nowrap;
  align-items: center;
  padding: 12px;
  display: flex;
}

.button--primary {
  background-color: #4f2ac3;
  font-size: 18px;
  font-weight: 700;
}

.event {
  padding: 80px 0;
}

.event--comidas {
  background-image: linear-gradient(#a7661b, #dbd82b);
}

.event--fogueira {
  background-image: linear-gradient(#dbd82b, #338922);
}

.event--dança {
  background-image: linear-gradient(#338922, #9c791d);
}

.event--image-left .container {
  flex-direction: row-reverse;
}

.event .container {
  gap: 40px;
  display: flex;
}

.event__details__title {
  margin-bottom: 16px;
  font-size: 18px;
  font-weight: bold;
}

.event__details__description {
  font-size: 16px;
  line-height: 22px;
}

.event__image {
  width: 360px;
  height: 196px;
}

* {
  box-sizing: border-box;
  color: #fff;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  text-decoration: none;
  list-style: none;
}

.container {
  width: 100%;
  max-width: 914px;
  margin: 0 auto;
}
